.button{padding:14px 25px;display:block;border:none;box-shadow:0 0 6px rgba(0,0,0,0.1);cursor:pointer;font-weight:600;font-size:14px}.button-primary{background:black;color:#FAFAFA}.button-transparent{background:None;box-shadow:none}.button-icon{width:100%;display:flex;align-items:center;justify-content:center}.button-icon svg{margin-left:10px;font-size:14px}.button-long-border{display:flex;align-items:center}.button-long-border .button-border{display:block;height:1px;width:100%;background:#d8d8d8}.button-long-border-button-wrap{width:100%;border:None;background:none;font-size:16px;display:flex;align-items:center;justify-content:center;font-weight:600}.button-long-border-icon{font-size:16px;margin-left:16px}.button-long-border-content-wrap{display:flex;align-items:center;justify-content:center;width:100%}

.input-wrap{display:flex;box-shadow:0 0 7px rgba(0,0,0,0.1);align-items:center}.input-wrap .input-icon{font-size:24px;padding:0  10px}.input-wrap .input{border:none;height:50px;font-weight:500;outline:none}

.login-page{display:flex;height:100vh;align-items:center;width:100vw}.login-page .hero-image-wrap{width:50%}.login-page .hero-image-wrap .hero-image{width:100%;height:100vh}.login-page .login-modal{margin-left:40px}.login-page .input-wrap{min-width:350px;margin-bottom:16px}

.homeBody {
    overflow: hidden;
}
.subbar__categories {
    margin: 20px auto;
    display: flex;
    max-width: 1246px;
    justify-content: center;
    min-width: 725px;
}
.subbarWrapper {
    width: 100%;
    border-bottom: solid 1px #D3D3D3;
}
.subbar__categoriesList {
    text-decoration: none;
    color: #034752;
    font-size: 16px;
    margin-left: 30px;
}
.subbar__categoriesList:hover {
    text-decoration: underline;
}
.homeHeader {
    display: flex;
    flex-direction: row;
}
.homeHeader__imgLg {
    width: 95%;
    max-width: 475px;
    max-height: 300px;
    cursor: pointer;
}
#homePageProgess {
    width: 95%;
}
.homeHeader__leftside {
    width: 50%;
    max-width: 500px;
    min-width: 325px;
    border-right: 1px solid #e8e8e8;
    margin-top: 25px;
    margin-left: auto;
    padding-right: 20px;
    padding-left: 25px;
}
#featuredLabel {
    margin-bottom: 20px;
    padding-top: 40px;
    font-size: 13px;
    color: rgb(101, 105, 105);
}
#featuredTitle {
    margin-top: 20px;
    width: 95%;
    max-width: 500px;
    font-size: 22px;
}
#featuredDes {
    margin-top: 20px;
    width: 95%;
    max-width: 500px;
    line-height: 1.25;
    padding-bottom: 10px;
}
#featuredBy {
    padding-bottom: 80px;
}
.homeHeader__rightside {
    padding-left: 50px;
    margin-right: auto;
    width: 50%;
    max-width: 500px;
    min-width: 325px;
    margin-top: 25px;
}
.homeHeader__imgSmall {
    width: 50%;
    max-width: 200px;
    cursor: pointer;
    margin-bottom: 15px;
}
.homeHeader__smallBoxes {
    display: flex;
    border-bottom: 1px solid #e8e8e8;
    padding-top: 15px;
}
#topSmallBox {
    padding-top: 0;
}
.homeHeader__tittle, .homeHeader__funded, .homeHeader__madeBy {
    margin-left: 20px;
    margin-bottom: 10px;
}
.homeHeader__funded {
    color: #037362;
}
.homeHeader__tittle {
    font-size: 14px;
}
.homeHeader__funded, .homeHeader__madeBy {
    font-size: 12px;
    line-height: 1.25;
}
.body {
    position: relative;
    display: flex;
    flex-direction: row;
    margin: 0 50px;
    padding-top: 25px;
    overflow-x: auto;
}
#listLabel {
    border-top: 1px solid #e8e8e8;
    margin-top: 50px;
    padding-top: 50px;
    margin-left: 25px;
}
.body__list {
    display: flex;
    flex-direction: column;
    margin-right: 10px;
}
.body__img {
    max-width: 250px;
    max-height: 250px;
    cursor: pointer;
}
#bodyTitle {
    font-size: 18px;
    margin: 24px 0;
}
#bodyDes {
    font-size: 14px;
    margin-bottom: 24px;
    line-height: 1.25;
}
#bodyBy {
    font-size: 12px;
    color: rgb(101, 105, 105);
    line-height: 1.25;
}
.vid-div {
    max-width: 300px;
    max-height: 300px;
}
.video-thumbnail{cursor:pointer;position:relative}.video-thumbnail>a{height:100%;width:100%;text-decoration:none}.video-thumbnail-img{box-shadow:0 0 15px rgba(0,0,0,0.2);object-fit:cover;width:100%}.video-thumbnail-text{margin-top:8px;color:black;text-decoration:none;font-weight:600}.video-thumbnail-popup-play{color:White;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);z-index:100}.video-thumbnail-popup-play-icon{font-size:40px}.video-thumbnail-popup-play-title{font-weight:700;font-size:18px}

.video-section{margin:56px 0px}.video-section-title{font-size:32px;font-weight:700;margin-bottom:40px}.video-section-view-more{margin-top:24px}.video-section-videos{display:flex;justify-content:space-between}.video-section-videos .video-thumbnail{width:23%;max-width:350px}

@media (min-width: 1025px) and (max-width: 1920px){.container{width:80%;margin:0 auto}}@media (min-width: 1921px){.container{width:1800px;margin:0 autO}}

.navbar{width:100%;box-shadow:0 0 15px rgba(0,0,0,0.2);height:70px}.navbar>.container{display:flex;justify-content:space-between;align-items:center;height:100%}.navbar-list-item{text-decoration:none;list-style:None}.navbar-logo{max-height:50px}.navbar-list-link{color:black;text-decoration:none;font-weight:600}.navbar-items-list{display:flex;align-items:center}.navbar-items-list .navbar-list-item:nth-child(2){margin:0 30px}

